html, body {
  width: 100%;
  height: 100%;
  font-family: 'Ubuntu';
  -webkit-font-smoothing: antialiased;
  background: #000000;
  line-height: 1.42;
}

body {
  margin: 0;
}

h1, h2, h3, h4, h5, h6 {
  margin-top: 0;
}

.wrapper {
  width: 100%;
  height: 100%;
}

.container {
  max-width: 940px;
  width: 940px;
  margin: 0 auto;
  position: relative;
  height: 100%;
}

.clearfix::before, .clearfix::after {
  content: "";
  width: 100%;
  clear: both;
  display: block;
}

.section {
  width: 100%;
  min-height: 650px;
  height: 100%;
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;
}

