.first_screen {
  background-image: url("../img/bg/first_screen.jpg");
}

.header {
  height: 80px;
}

.header__left {
  width: 25%;
  float: left;
}

.header__right {
  width: 75%;
  float: right;
}

.header__logo {
  margin-top: 5px;
}

.header__img {
  height: 70px;
  width: auto;
}

.menu {
  text-align: right;
}

.menu__list {
  list-style-type: none;
  margin: 25px 0 0 0;
  padding: 0;
  display: inline-block;
}

.menu__item {
  display: inline-block;
  margin: 0;
  padding: 0 10px;
  vertical-align: middle;
  text-align: center;
}

.menu__link {
  font-family: 'Ubuntu-Medium';
  font-size: 13px;
  line-height: 30px;
  color: #fff;
  text-decoration: none;
  display: block;
}

.menu__link:hover {
  color: rgba(255, 255, 255, 0.7);
}

.menu__decor {
  display: inline-block;
  width: 4px;
  height: 4px;
  border-radius: 50%;
  background-color: #fff;
  vertical-align: middle;
}

.menu__order {
  background-color: #e45028;
  border-radius: 5px;
  font-family: 'Ubuntu-Medium';
  font-size: 13px;
  line-height: 30px;
  color: #fff;
  text-decoration: none;
  display: inline-block;
  padding: 5px 20px;
  vertical-align: middle;
}

.menu__order:hover {
  background-color: #c1401e;
}

.content-wrapper {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

.main_wrapper {
  top: 80px;
}

.content {
  width: 900px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.main_content__left {
  width: 425px;
  float: left;
  position: relative;
}

.main_content__right {
  width: 430px;
  float: right;
  margin-left: 40px;
}

.main_content__img {
  width: 417px;
  height: auto;
}

.main_content__drops {
  position: absolute;
  left: -50px;
  top: -50px;
  width: 500px;
  height: auto;
}

.main_content__title {
  font-size: 72px;
  line-height: 85px;
  color: #f9b43b;
  font-family: 'Gagalin-Regular';
  margin: 0;
}

.main_content__text {
  font-size: 65px;
  line-height: 85px;
  font-family: 'Lifehack-Medium';
  margin: 0;
  color: #fff;
}

.arrow-scroll {
  position: absolute;
  bottom: 40px;
  left: 50%;
  transform: translate 0, -50%;
}

.side_menu {
  position: absolute;
  top: 20%;
  left: 105%;
  z-index: 1000;
}

.side_menu__list {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.side_menu__item {
  display: block;
  height: 6px;
  width: 6px;
  border-radius: 50%;
  background-color: #fff;
  margin-bottom: 16px;
}

.we_screen {
  background-image: url("../img/bg/we_screen.jpg");
}

.we__title {
  text-align: center;
  font-size: 72px;
  line-height: 85px;
  color: #2f3234;
  font-family: 'Gagalin-Regular';
  margin: 0 0 75px 0;
}

.we_row__col {
  width: 32%;
  display: inline-block;
  float: left;
  padding: 0 5px;
  text-align: center;
}

.we_row__logo {
  height: 85px;
  width: auto;
}

.we__text-title {
  font-size: 24px;
  line-height: 28px;
  font-family: 'Lifehack-Medium';
  color: #2f3234;
  margin-top: 30px;
}

.we__text {
  font-size: 14px;
  line-height: 24px;
  font-family: 'Ubuntu-Light';
  color: #2f3234;
  margin: 0;
}
